.auth-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0f0c29 0%, #302b63 50%, #24243e 100%);
  position: relative;
  overflow: hidden;
  font-family: 'Inter', sans-serif;
}

/* Floating animated blobs */
.auth-blob {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.4;
  animation: blobFloat 8s ease-in-out infinite;
}

.auth-blob-1 {
  width: 400px;
  height: 400px;
  background: #6c63ff;
  top: -100px;
  left: -100px;
  animation-delay: 0s;
}

.auth-blob-2 {
  width: 300px;
  height: 300px;
  background: #00d2ff;
  bottom: -50px;
  right: -50px;
  animation-delay: -3s;
}

.auth-blob-3 {
  width: 250px;
  height: 250px;
  background: #a855f7;
  top: 50%;
  left: 50%;
  animation-delay: -5s;
}

@keyframes blobFloat {
  0%, 100% { transform: translate(0, 0) scale(1); }
  25% { transform: translate(30px, -30px) scale(1.05); }
  50% { transform: translate(-20px, 20px) scale(0.95); }
  75% { transform: translate(20px, 10px) scale(1.02); }
}

.auth-card {
  position: relative;
  z-index: 1;
  width: 400px;
  padding: 3rem 2.5rem;
  background: rgba(255, 255, 255, 0.06);
  backdrop-filter: blur(24px);
  -webkit-backdrop-filter: blur(24px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 24px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.4);
  color: #fff;
  text-align: center;
}

.auth-logo {
  font-size: 3rem;
  margin-bottom: 0.5rem;
}

.auth-title {
  font-size: 1.8rem;
  font-weight: 700;
  margin: 0 0 0.3rem 0;
  letter-spacing: -0.5px;
}

.auth-subtitle {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.95rem;
  margin: 0 0 2rem 0;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.input-group {
  position: relative;
  display: flex;
  align-items: center;
}

.input-icon {
  position: absolute;
  left: 16px;
  color: rgba(255, 255, 255, 0.4);
  font-size: 1.1rem;
  pointer-events: none;
}

.input-group input {
  width: 100%;
  padding: 0.9rem 1rem 0.9rem 2.8rem;
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 14px;
  color: #fff;
  font-size: 0.95rem;
  font-family: 'Inter', sans-serif;
  outline: none;
  transition: border-color 0.3s, background 0.3s;
}

.input-group input::placeholder {
  color: rgba(255, 255, 255, 0.35);
}

.input-group input:focus {
  border-color: #6c63ff;
  background: rgba(255, 255, 255, 0.12);
}

.auth-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  width: 100%;
  padding: 0.95rem;
  margin-top: 0.5rem;
  background: linear-gradient(135deg, #6c63ff 0%, #a855f7 100%);
  border: none;
  border-radius: 14px;
  color: #fff;
  font-size: 1rem;
  font-weight: 600;
  font-family: 'Inter', sans-serif;
  cursor: pointer;
  transition: box-shadow 0.3s, opacity 0.3s;
}

.auth-btn:hover {
  box-shadow: 0 4px 24px rgba(108, 99, 255, 0.4);
}

.auth-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.btn-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top-color: #fff;
  border-radius: 50%;
  animation: spin 0.6s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.auth-toggle {
  margin-top: 1.5rem;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.5);
}

.auth-toggle span {
  color: #a78bfa;
  font-weight: 600;
  cursor: pointer;
  margin-left: 0.3rem;
  transition: color 0.2s;
}

.auth-toggle span:hover {
  color: #c4b5fd;
}

@media (max-width: 480px) {
  .auth-card {
    width: 92vw;
    padding: 2rem 1.5rem;
  }
}
